:root {
  --colorBackground: #f4f4f4;
  --colorTextDark: #111321;
  --colorTextLight: #f4f4f4;
  --toglerMainColor: #4440f6;
  --toglerMinorColor: #fff;
  --toglerIconSunColor: #4b48db;
  --toglerIconMoonColor: #5f6775;
}

* {
  margin: 0;
  padding: 0;
}

body {
  color: var(--colorTextDark);
  background-color: var(--colorBackground);
  font-family: Manrope, sans-serif;
  font-size: 16px;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

img {
  height: auto;
  display: block;
}

p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
}

textarea {
  resize: none;
}

button {
  font-family: Manrope, sans-serif;
}

body.dark {
  --colorBackground: #2e2e2e;
  --colorTextDark: #f4f4f4;
  --colorTextLight: #f4f4f4;
  --toglerMainColor: #fff;
  --toglerMinorColor: #4440f6;
  --toglerIconSunColor: #5f6775;
  --toglerIconMoonColor: #4b48db;
}

.container {
  padding: 0 16px;
}

@media screen and (min-width: 320px) {
  .container {
    width: 320px;
    margin: 0 auto;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
  }
}

.theme {
  gap: 8px;
  display: flex;
}

@media screen and (min-width: 1280px) {
  .theme {
    gap: 12px;
  }
}

.theme__caption {
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
  display: none;
}

@media screen and (min-width: 1280px) {
  .theme__caption {
    display: block;
  }
}

.theme__caption--light {
  color: var(--toglerIconSunColor);
}

.theme__caption--dark {
  color: var(--toglerIconMoonColor);
}

@media screen and (min-width: 1280px) {
  .theme__icon {
    display: none;
  }
}

.theme__icon--sun {
  fill: var(--toglerIconSunColor);
}

.theme__icon--moon {
  fill: var(--toglerIconMoonColor);
}

.theme__togler {
  width: 40px;
  height: 20px;
  border: 1px solid var(--toglerMainColor);
  background-color: var(--toglerMinorColor);
  border-radius: 10px;
  margin-bottom: 20px;
  padding: 1px;
  position: relative;
}

.theme__lever {
  width: 16px;
  height: 16px;
  background-color: var(--toglerMainColor);
  border-radius: 50%;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 1px;
  left: 1px;
}

body.dark .theme__lever {
  transform: translateX(20px);
}

footer {
  height: 120px;
  background-color: #7a7a7a;
}

@media screen and (min-width: 768px) {
  footer {
    height: 146px;
  }
}

