$fontFamilyMain: 'Manrope', sans-serif;
$fontFamilyMinor: 'Poppins', sans-serif;
$fontFamilyBanner: 'Roboto', sans-serif;

$colorAccent: #4440f6;
$colorFooterBackground: #7a7a7a;

:root {
    --colorBackground: #f4f4f4;
    --colorTextDark: #111321;
    --colorTextLight: #f4f4f4;
    --toglerMainColor: #4440f6;
    --toglerMinorColor: #fff;
    --toglerIconSunColor: #4b48db;
    --toglerIconMoonColor: #5f6775;
}

$stickerBackground: linear-gradient(
    rgba(68, 64, 247, 0.7),
    rgba(68, 64, 247, 0.7)
);

$baseTransition: 250ms cubic-bezier(0.4, 0, 0.2, 1);
